import { Component, OnInit,ViewEncapsulation } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {appConfig} from "../app.config";
import {NgbModal, ModalDismissReasons,NgbModalRef} from '@ng-bootstrap/ng-bootstrap';


@Component({
  selector: 'app-tree',
  templateUrl: './tree.component.html',
  styleUrls: ['./tree.component.css'],
  encapsulation: ViewEncapsulation.None,
  styles: [`
    .dark-modal .modal-content {
      background-color: #292b2c;
      color: white;
    }
    .dark-modal .close {
      color: white;
    }
    .light-blue-backdrop {
      background-color: #5cb3fd;
    }
  `]
})
export class TreeComponent implements OnInit {
  
  public nodeList = [];
  public nodeListButRoot=[];
  public targetNode;
  public newNodeName;
  public newNodeParent;
  public newNodeChild;
  public targetNodeParents=[];
  public targetNodeChildren=[];
  config = {
    displayKey:"node_name", //if objects array passed which key to be displayed defaults to description,
    search:true //enables the search plugin to search in the list
    }
  constructor(private httpClient: HttpClient,private modalService: NgbModal) { }

  ngOnInit() {
    this.httpClient.get(appConfig.apiUrl+'/TreeNodes').subscribe((res: any) => {
      this.nodeList=res.data;
    }, err => {
      console.log(err.error.msg);
    });
    this.httpClient.get(appConfig.apiUrl+'/TreeNodesButRoot').subscribe((res: any) => {
      this.nodeListButRoot=res.data;
    }, err => {
      console.log(err.error.msg);
    });
      
  }

  

  openVerticallyCentered(content) {
    this.modalService.open(content, { centered: true,windowClass: 'dark-modal' });
  }

  targetNodeToView(nodename){
    this.targetNode=nodename;
  }

  nodeParents(nodeId){
    this.httpClient.get(appConfig.apiUrl+'/Parents/'+nodeId).subscribe((res: any) => {
      this.targetNodeParents=res.data;
    }, err => {
      console.log(err.error.msg);
    });
  }

  nodeChildren(nodeId){
    this.httpClient.get(appConfig.apiUrl+'/Children/'+nodeId).subscribe((res: any) => {
      this.targetNodeChildren=res.data;
    }, err => {
      console.log(err.error.msg);
    });
  }
//the function name can't be the same as the form name!
  createNewNode(){
    if(this.nodeList.length>0){
    if(this.newNodeParent && this.newNodeName){
    console.log("newNodeName:",this.newNodeName);
    console.log("newNodeParent",this.newNodeParent[0]._id);
     if(this.newNodeChild){
           let newNode={
                  node_name:this.newNodeName,
                  direct_parent:this.newNodeParent[0]._id,
                  direct_parent_name:this.newNodeParent[0].node_name,
                  child:this.newNodeChild[0]._id
                       }
                       this.httpClient.post(appConfig.apiUrl+'/createNode',newNode).subscribe((res: any) => {
                        this.newNodeName=undefined;
                        this.newNodeParent=undefined;
                        this.newNodeChild=undefined;
                        this.ngOnInit();
                      }, err => {
                        this.newNodeName=undefined;
                        this.newNodeParent=undefined;
                        this.newNodeChild=undefined;
                        console.log(err.error.msg);
                      });           
       }else{
        let newNode={
          node_name:this.newNodeName,
          direct_parent:this.newNodeParent[0]._id,
          direct_parent_name:this.newNodeParent[0].node_name,

               }
               this.httpClient.post(appConfig.apiUrl+'/createNode',newNode).subscribe((res: any) => {
                 this.newNodeName=undefined;
                 this.newNodeParent=undefined;
                 this.newNodeChild=undefined;
                this.ngOnInit();
              }, err => {
                this.newNodeName=undefined;
                this.newNodeParent=undefined;
                this.newNodeChild=undefined;
                console.log(err.error.msg);
              });
      }

    } else
    console.log("please Choose a name & parent");
  }
  
  else{
    //Root Creation
    if(this.newNodeName){
      let newNode={
        node_name:this.newNodeName
      }
      this.httpClient.post(appConfig.apiUrl+'/createNode',newNode).subscribe((res: any) => {
        this.newNodeName=undefined;
        this.newNodeParent=undefined;
        this.newNodeChild=undefined;
       this.ngOnInit();
       
     }, err => {
      this.newNodeName=undefined;
      this.newNodeParent=undefined;
      this.newNodeChild=undefined;
       console.log(err.error.msg);
     });
    }else
    console.log("please Choose a name");
  }
}

clearTree(){
  this.httpClient.delete(appConfig.apiUrl+'/clearTree').subscribe((res: any) => {
    this.ngOnInit();
  }, err => {
    console.log(err.error.msg);
  });
}
}


